.sidebar {
    display: flex;
    flex-direction: column;
    flex: 0.35;
  }
  
  .sidebar__header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
  }
  
  .sidebar__headerRight {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
  }
  
  .sidebar__headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
  }
  
  .sidebar__search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
  }
  
  .sidebar__searchContainer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
  }
  
  .sidebar__searchContainer > .MuiSvgIcon-root {
    color: grey;
    padding: 10px;
    margin-right: 18px;
  }
  
  .sidebar__searchContainer > input {
    border: none;
    width: 90%;
  }
  
  .sidebar__searchContainer > input:focus {
    outline-width: 0;
  }
  
  .sidebar__chats {
    flex: 1;
    background-color: white;
    overflow-x: hidden;
    overflow-y: auto;
  }